import React from 'react';
import s from './WebsiteAuthor.module.css';

const WebsiteAuthor = () => {
    return (
        <div className={s.websiteAuthor}>
            <p className={s.made}>Made with ❤️ by Tigran Kheranyan</p>
            <p className={s.themeBy}>Theme by <a className={s.myPortfolio} href="#"> TigranKheranyan-portflolio</a></p>
        </div>
    );
}

export default WebsiteAuthor;