import React from 'react';
import styles from './BigProjects.module.css';
import BigProjectCard from './BigProjectCard/BigProjectCard';

const BigProjects = () => {
    let cards = [
        {
            imgSrc: 'https://c.tenor.com/aXPGXXcE1NwAAAAi/hehe-boi.gif',
            imgAlt: "itworld's image",
            name: 'itWorld',
            description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit.'
        },
        {
            imgSrc: 'https://c.tenor.com/aXPGXXcE1NwAAAAi/hehe-boi.gif',
            imgAlt: "avto's image",
            name: 'Avto',
            description: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit.'
        },
    ];

    return (
        <div className={styles.bigProjectsWrapper}>
            <span className={styles.bigProjects}>Big Projects</span>
            <p className={styles.description}>SOME STARTUPS AND COMPANIES THAT I HELPED TO CREATE THEIR TECH</p>
            <div className={styles.cards}>
                {cards.map(card => <BigProjectCard imgSrc={card.imgSrc}
                    imgAlt={card.imgAlt}
                    name={card.name}
                    description={card.description} />)}
            </div>
        </div>
    );
}

export default BigProjects;