import React from 'react';
import s from './Skills.module.css';
import Tikoit from '../../assets/images/Tikoit.jpg';
import ProgrammingLanguage from './ProgrammingLanguage/ProgrammingLanguage';

const Skills = () => {
    const programmingLanguages = [
        {
            imgSrc: 'https://webref.ru/assets/images/book/html5.png',
            imgAlt: 'HTML icon',
            languageName: 'HTML',
        },
        {
            imgSrc: 'https://www.toolbox.am/img/trainings/icons//css3-logo.png',
            imgAlt: 'CSS icon',
            languageName: 'CSS',
        },
        {
            imgSrc: 'https://html5hive.org/wp-content/uploads/2014/06/js_800x800.jpg',
            imgAlt: 'JavaScript icon',
            languageName: 'JavaScript',
        },
        {
            imgSrc: 'https://avatars.githubusercontent.com/u/70142?s=200&v=4',
            imgAlt: 'jQuery icon',
            languageName: 'jQuery',
        },
        {
            imgSrc: 'https://sap.github.io/ui5-webcomponents/assets/images/react.svg',
            imgAlt: 'React icon',
            languageName: 'React',
        },
        {
            imgSrc: 'https://cdn.iconscout.com/icon/free/png-512/redux-283024.png',
            imgAlt: 'Redux icon',
            languageName: 'Redux',
        },
        {
            imgSrc: 'https://cdn-icons-png.flaticon.com/512/174/174881.png',
            imgAlt: 'Wordpress icon',
            languageName: 'Wordpress',
        },
    ];

    return (
        <div id='skills' className={s.skills}>
            <img className={s.Tigran_img} src={Tikoit} alt='Tigran Kheranyan programming in TUMO' />
            <div className={s.whatIDo}>
                <span className={s.header}>Ինչ եմ անում ես</span>
                <p className={s.about_me}>CRAZY FULL STACK DEVELOPER WHO WANTS TO EXPLORE EVERY TECH STACK</p>
                <div className={s.languages}>
                    {programmingLanguages.map(programmingLanguage => <ProgrammingLanguage key={programmingLanguage.languageName} imgSrc={programmingLanguage.imgSrc} imgAlt={programmingLanguage.imgAlt} languageName={programmingLanguage.languageName} />)}
                </div>
                <div className={s.mySkillsWrapper}>
                    <p>⚡Developing frontend for websites.</p>
                    <p>⚡Lorem ipsum dolor sit amet consectetur adipisicing elit. Quod?</p>
                    <p>⚡Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempora sequi aut aspernatur.</p>
                </div>
            </div>
        </div>
    );
}

export default Skills;