import React from 'react';
import styles from './Footer.module.css';
import SmartHome from '../../assets/images/SmartHome.jpg'
import SocImages from '../../CommonComponents/SocImages/SocImages';


function Footer() {
    return (
        <footer id='footer'>
            <p className={styles.header}>Reach Out to me!</p>
            <div className={styles.howToReachOutToMe}>
                <div className={styles.leftBlock}>
                    <p className={styles.description}>DISCUSS A PROJECT OR JUST WANT TO SAY HI? MY INBOX IS OPEN FOR ALL.</p>
                    <p className={styles.aboutMe}>"Iron man | Aspiring Software Developer 👾 | Open Source Contributor 🔥 | Speaker🎙 | Tech Writer ✍️ "</p>
                    <p className={styles.location}>📍Yerevan, Armenia</p>
                    <SocImages componentForUse='footer' />
                </div>
                <img className={styles.myImage} src={SmartHome} alt='Tigran Kheranyan Photo' />
            </div>
        </footer>
    );
}

export default Footer