import React, { useContext, useState } from 'react';
import { Link } from 'react-scroll'
import nightModeIcon from '../../assets/images/1x/nightModeIcon.png';
import { useTheme, useThemeUpdate } from '../../ThemeContext';
import styles from './Navbar.module.css';

const Navbar = () => {
    const [hamburgerToggle, setHamburgerToggle] = useState(false);

    const toggleHamburger = () => {
        setHamburgerToggle(!hamburgerToggle);
    }

    const darkTheme = useTheme();
    const toggleTheme = useThemeUpdate();

    console.log(styles.darkThemeActiveNavItems);

    return (
        <nav className={styles.navbar}>
            <Link className={styles.logo} to='/home' spy={true} smooth={true}>TigranMets </Link>
            <div className={`${!darkTheme ? styles.menuItems : styles.darkThemeActiveNavItems} ${hamburgerToggle && styles.hamburgerActiveNavItems}`}>
                <Link to='skills' spy={true} smooth={true} >Skills</Link>
                <Link to='profilciency' spy={true} smooth={true}>Proficiency</Link>
                <Link to='experiences' spy={true} smooth={true}>Work Experiences</Link>
                <Link to='projects' spy={true} smooth={true}>Projects</Link>
                <Link to='achievements' spy={true} smooth={true}>Achievements</Link>
                <Link to='blogs' spy={true} smooth={true}>Blogs</Link>
                <Link to='contact_me' spy={true} smhooth={true}>Contact Me</Link>
                <div className={!darkTheme ? styles.themeToggle : styles.darkModeActiveToggle}>
                    <input id='toggle' className={styles.toggleControl} type='checkbox' />
                    <label for='toggle' onClick={toggleTheme}></label>
                </div>
            </div>
            <img className={styles.hamburgerMenu} src='https://cdn-icons-png.flaticon.com/512/56/56763.png' alt='hamburger menu' onClick={toggleHamburger} />
        </nav>
    );
}

export default Navbar;