import React from 'react';
import styles from './Experiences.module.css';

function ExperiencesCard(props) {
    return (
        <div className={styles.experiences_card}>
            <div className={styles.company}>
                <p className={styles.company_name}>{props.companyName}</p>
                <img className={styles.company_img} src={props.companyImageSrc} alt={props.companyName} />
            </div>
            <div className={styles.aboutExperience}>
                <p className={styles.job}>{props.job}</p>
                <p className={styles.date}>{props.jobExperience}</p>
                <p className={styles.impressions}>{props.impressions}</p>
            </div>
        </div>
    );
}

export default ExperiencesCard;