import React, { useRef, useState } from 'react';
import { useEffect } from 'react';
import styles from './OpenSourceProjectCard.module.css';

const ProjectCard = (props) => {

    const aboutProjectRef = useRef(null);

    return (
        <a href="#" className={styles.openSoruceProjectCardLinkWrapper}>
            <div className={styles.projectCard}>
                <p className={styles.project_name}>{props.header}</p>
                <p className={styles.about_project} ref={aboutProjectRef}>{props.description}</p>
                <div className={styles.projectLanguageWrapper}>
                    <li>{props.projectLanguage}</li>
                    <span className={styles.kb}>{props.projectSize}</span>
                </div>
            </div>
            {console.log('some log')}
        </a>
    );
}

export default ProjectCard;